import parseUrl from "parse-url";

let win = null;
export default (editor, opts = {}) => {

    let htmlBlockEditCommand = 'abantecart-html-block-additional-settings';
    let listingBlockEditCommand = 'abantecart-listing-block-additional-settings';

    // Add command
    editor.Commands.add(htmlBlockEditCommand, {
        run(editor) {
            const target = editor.getSelected();
            const getFirstNumber = (potentialNumbers) => {
                for(let i = 0; i < potentialNumbers.length; i++) {
                  const value = potentialNumbers[i];

                  if (typeof value === 'number') {
                    return value;
                  }
                }
              };
            if (target) {
                let cfg = editor.getConfig();
                let editUrl = cfg.pluginsOpts['grapesjs-abantecart-component'].edit_urls.abantecart_static_block;
                let Query = parseUrl(editUrl, true);
                Query.query.token = cfg.pluginsOpts['grapesjs-abantecart-component'].abc_token;
                Query.query.custom_block_id = target.attributes.custom_block_id;
                let w = getFirstNumber([window.innerWidth, document.documentElement.clientWidth, screen.width])-100;
                let h = getFirstNumber([window.innerHeight, document.documentElement.clientHeight, screen.height])-50;
                let y = window.top.outerHeight / 2 + window.top.screenY - (h / 2);
                let x = window.top.outerWidth / 2 + window.top.screenX - (w / 2);
                win = window.open(
                    Query.protocol
                    + '://' + Query.resource + Query.pathname + '?'
                    + 'rt=' + Query.query.rt
                    + '&s=' + Query.query.s
                    + '&custom_block_id=' + Query.query.custom_block_id
                    + '&token=' + Query.query.token
                );
                win.onbeforeunload = function () {
                    setTimeout(function () {
                        target.getView().render();
                    }, 3000);
                };
            }
        },
    });
    // Add command
    editor.Commands.add(listingBlockEditCommand, {
        run(editor) {
            const target = editor.getSelected();
            const getFirstNumber = (potentialNumbers) => {
                for(let i = 0; i < potentialNumbers.length; i++) {
                  const value = potentialNumbers[i];

                  if (typeof value === 'number') {
                    return value;
                  }
                }
              };
            if (target) {
                let cfg = editor.getConfig();
                let editUrl = cfg.pluginsOpts['grapesjs-abantecart-component'].edit_urls.abantecart_static_block;
                let Query = parseUrl(editUrl, true);
                Query.query.token = cfg.pluginsOpts['grapesjs-abantecart-component'].abc_token;
                Query.query.custom_block_id = target.attributes.custom_block_id;
                // let w = getFirstNumber([window.innerWidth, document.documentElement.clientWidth, screen.width])-100;
                // let h = getFirstNumber([window.innerHeight, document.documentElement.clientHeight, screen.height])-50;
                // let y = window.top.outerHeight / 2 + window.top.screenY - (h / 2);
                // let x = window.top.outerWidth / 2 + window.top.screenX - (w / 2);
                win = window.open(
                    Query.protocol
                    + '://' + Query.resource + Query.pathname + '?'
                    + 'rt=' + Query.query.rt
                    + '&s=' + Query.query.s
                    + '&custom_block_id=' + Query.query.custom_block_id
                    + '&token=' + Query.query.token
                );
                win.onbeforeunload = function () {
                    setTimeout(function () {
                        target.getView().render();
                    }, 3000);
                };
            }
        },
    });
};