export default (editor, opts = {}) => {
    const bm = editor.BlockManager;
    if (opts.blocks) {
        for (var k in opts.blocks) {
            var block = opts.blocks[k];
            if(!block.name){ continue; }
            bm.add(block.name, block.opts);
        }
    }
    if (opts.mainContentArea) {
        bm.add('main_content_area',
            {
                category: {
                    id: 'main_content_area',
                    label: 'Storefront Content',
                    open: false
                },
                label: 'Main Content Area',
                content: opts.mainContentArea,
                attributes: {class: 'fa fa-5x fa-window-maximize main-content-area'}
            }
        );
    }

    bm.add('abc_mp',
        {
            label: 'Get More Blocks...',
            attributes: { class: 'fa fa-5x fa-external-link' },
            draggable: false,
            editable: false,
            droppable: false,
            disable: true,
            onClick: function(){ window.open('https://marketplace.abantecart.com/blocks'); },
        }
        );

}
