import axios from "axios";
import qs from "qs";

export default (editor, opts = {}) => {
    const domc = editor.DomComponents;
    const dataGjsAttribs = ['custom-name', 'route', 'type', 'page_id', 'layout_id', 'custom_block_id'];
 //   console.log(opts.blocks);
    try {
        //do the trick to set ID for container anyway. This needed in the Render
        editor.on('block:drag:start', function (component, ev) {
            if (!component) return;
            if (component.attributes.id === 'main_content_area') {
                let wrapper = editor.getWrapper();
                if (wrapper.find('[data-gjs-type="abantecart-main-content-area"]').length > 0) {
                    alert('Content Area already presents in the canvas!');
                    ev.preventDefault();
                }
            }
        });
        //do the trick to set ID for container anyway. This needed in the Render
        editor.on('block:drag:stop', component => {
            if (!component) return;
            component.addAttributes({
                'id': component.getId()
            });
        });

        if (opts.mainContentArea) {
            domc.addType('abantecart-main-content-area', {
                model: {
                    defaults: {
                        droppable: false,
                        removable: false,
                        ...opts
                    }
                },
                view: {
                    async onRender({el, model}) {
                        let attribs = model.attributes;
                        try {
                            let queryParams = {
                                route: attribs.route,
                            };
                            if (attribs.params) {
                                for (var attrname in attribs.params) {
                                    queryParams[attrname] = attribs.params[attrname];
                                }
                            }
                            //sign for render
                            queryParams.render_mode = 'editor';

                            let {data} = await axios.get(opts.storeUrl + '&' + qs.stringify(queryParams));
                            let innerData = document.createElement('div');
                            //fill tag attributes for stable re-import of pure html
                            for (let at in dataGjsAttribs) {
                                let name = dataGjsAttribs[at];
                                if (attribs.mainContentArea.hasOwnProperty(name)) {
                                    model.attributes.attributes['data-gjs-' + name] = attribs.mainContentArea[name];
                                }
                            }

                            innerData.setAttribute("id", model.ccid);
                            innerData.innerHTML = data;
                            el.appendChild(innerData);
                        } catch (err) {
                            console.log(err);
                        }
                    }
                },
            });
        }

        domc.addType('abantecart-generic-block', {
            model: {
                defaults: {
                    tagName: 'div',
                    droppable: false,
                    traits(component) {
                        const result = [];
                        let currTpl = component.attributes.template
                            ?? (component.attributes.templates ? component.attributes.templates[0].id : component.attributes.template);
                        const customName = component.attributes['custom-name'].toUpperCase();
                        if(!opts.blocks[customName]){
                            console.log(customName + ' not found in the block list!');
                            return false;
                        }
                        result.push(
                            {
                                name: 'data-gjs-template',
                                label: 'Block Template',
                                type: 'select',
                                options: opts.blocks[customName].opts.content.templates ?? [currTpl],
                                default: currTpl,
                                changeProp: 0,
                            }
                        );
                        return result;
                    },
                    ...opts
                },
                updated(property, value, prevValue) {
                    if(property === 'status' &&
                        (value === 'hovered' || prevValue === 'hovered' )
                    ){
                        return;
                    }
                    this.trigger('rerender');
                },
            },
            view: {
                async onRender({el, model}) {
                    let attribs = model.attributes;
                    let queryParams = opts.mainContentArea.params && opts.mainContentArea.params.length
                        ? opts.mainContentArea.params
                        : {};
                    if (opts.mainContentArea.template.length) {
                        queryParams.pageTemplate = opts.mainContentArea.template;
                    }
                    queryParams.route = attribs.route;
                    queryParams.template = attribs.attributes['data-gjs-template'];
                    queryParams.layout_id = opts.mainContentArea.layout_id;
                    queryParams.page_id = opts.mainContentArea.page_id;

                    if (attribs.custom_block_id.length) {
                        queryParams.custom_block_id = attribs.custom_block_id;
                    }
                    //sign for render
                    queryParams.render_mode = 'editor';

                    let {data} = await axios.get(opts.storeUrl + '&' + qs.stringify(queryParams));
                    let innerData = document.createElement('div');
                    //fill tag attributes for stable re-import of pure html
                    for (let at in dataGjsAttribs) {
                        let name = dataGjsAttribs[at];
                        if (!attribs.attributes.hasOwnProperty(name) && model._previousAttributes.hasOwnProperty(name)) {
                            model.attributes.attributes['data-gjs-' + name] = model._previousAttributes[name];
                        }
                    }

                    innerData.innerHTML = data;
                    el.appendChild(innerData);
                }
            },
        });

        domc.addType('abantecart-static-block', {
            model:
                {
                    defaults: {
                        droppable: false,
                        traits(component) {
                            const result = [];
                            let currTpl = component.attributes.template
                                ?? (component.attributes.templates ? component.attributes.templates[0].id : component.attributes.template);
                            const customName = component.attributes['custom-name'].toUpperCase();
                            if(!opts.blocks[customName]){
                                console.log(customName + ' not found in the block list!');
                                return false;
                            }
                            let tpls = opts.blocks[customName].opts.content.templates ?? [currTpl];
                            result.push(
                                {
                                    name: 'data-gjs-template',
                                    label: 'Block Template',
                                    type: 'select',
                                    options: tpls,
                                    default: currTpl,
                                    changeProp: 0,
                                }
                            );
                            return result;
                        },
                        ...opts
                    },
                    /**
                     * Initialize the component
                     */
                    init() {
                        const toolbar = this.get('toolbar');
                        const id = 'edit_block';

                        // Add the custom code toolbar button if requested and it's not already in
                        if (!toolbar.filter(tlb => tlb.id === id).length) {
                            toolbar.unshift({
                                id,
                                command: 'abantecart-html-block-additional-settings',
                                attributes: {
                                    class: 'fa fa-gears',
                                    title: 'Open additional settings of block'
                                },
                            });
                        }
                        this.get('traits').push({
                            type: 'button',
                            text: 'Additional Settings',
                            command: 'abantecart-html-block-additional-settings',
                        });
                    },
                    updated(property, value, prevValue) {
                        if(property === 'status' &&
                            (value === 'hovered' || prevValue === 'hovered' )
                        ){
                            return;
                        }
                        this.trigger('rerender');
                    },
                },
            view: {
                async onRender({el, model}) {
                    let attribs = model.attributes;
                    try {
                        let queryParams = {
                            route: attribs.route,
                            custom_block_id: attribs.custom_block_id,
                        };
                        if (opts.mainContentArea.template.length) {
                            queryParams.pageTemplate = opts.mainContentArea.template;
                        }
                        queryParams.template = attribs.attributes['data-gjs-template'];
                        //sign for render
                        queryParams.render_mode = 'editor';
                        let {data} = await axios.get(opts.storeUrl + '&' + qs.stringify(queryParams));
                        let innerData = document.createElement('div');
                        innerData.setAttribute("id", model.ccid);
                        //fill tag attributes for stable re-import of pure html
                        for (let at in dataGjsAttribs) {
                            let name = dataGjsAttribs[at];
                            if (!attribs.attributes.hasOwnProperty(name) && model._previousAttributes.hasOwnProperty(name)) {
                                model.attributes.attributes['data-gjs-' + name] = model._previousAttributes[name];
                            }
                        }
                        innerData.innerHTML = data;
                        el.appendChild(innerData);
                    } catch (err) {
                        console.log(err);
                    }
                }
            },
        });
        domc.addType('abantecart-listing-block', {
            model: {
                defaults: {
                    droppable: false,
                    traits(component) {
                        const result = [];
                        let currTpl = component.attributes.template
                            ?? (component.attributes.templates ? component.attributes.templates[0].id : component.attributes.template);
                        const customName = component.attributes['custom-name'].toUpperCase();
                        if(!opts.blocks[customName]){
                            console.log(customName + ' not found in the block list!');
                            return false;
                        }
                        result.push(
                            {
                                name: 'data-gjs-template',
                                label: 'Block Template',
                                type: 'select',
                                options: opts.blocks[customName].opts.content.templates ?? [currTpl],
                                default: currTpl,
                                changeProp: 0,
                            }
                        );
                        return result;
                    },
                },
                /**
                 * Initialize the component
                 */
                init() {
                    const toolbar = this.get('toolbar');
                    const id = 'edit_list_block';

                    // Add the custom code toolbar button if requested and it's not already in
                    if (!toolbar.filter(tlb => tlb.id === id).length) {
                        toolbar.unshift({
                            id,
                            command: 'abantecart-listing-block-additional-settings',
                            attributes: {
                                class: 'fa fa-gears',
                                title: 'Open additional settings of block'
                            },
                        });
                    }
                    this.get('traits').push({
                        type: 'button',
                        text: 'Additional Settings',
                        command: 'abantecart-listing-block-additional-settings',
                    });
                },
                updated(property, value, prevValue) {
                    if(property === 'status' &&
                        (value === 'hovered' || prevValue === 'hovered' )
                    ){
                        return;
                    }
                    this.trigger('rerender');
                },
            },
            view: {
                async onRender({el, model}) {
                    let attribs = model.attributes;
                    let queryParams = {
                        route: attribs.route,
                        custom_block_id: attribs.custom_block_id,
                    };
                    //sign for render
                    queryParams.render_mode = 'editor';
                    let {data} = await axios.get(opts.storeUrl + '&' + qs.stringify(queryParams));
                    let innerData = document.createElement('div');
                    if (attribs.params) {
                        for (var attrname in attribs.params) {
                            el.setAttribute(
                                attrname,
                                attribs.params[attrname]
                            );
                        }
                    }
                    innerData.setAttribute("id", model.ccid);
                    //fill tag attributes for stable re-import of pure html
                    for (let at in dataGjsAttribs) {
                        let name = dataGjsAttribs[at];
                        if (!attribs.attributes.hasOwnProperty(name) && model._previousAttributes.hasOwnProperty(name)) {
                            model.attributes.attributes['data-gjs-' + name] = model._previousAttributes[name];
                        }
                    }
                    innerData.innerHTML = data;
                    el.appendChild(innerData);
                }
            },
        });
    } catch (err) {
        console.log(err);
        logToAbc(opts, err);
    }

    async function logToAbc(opts, e) {
        if (opts.loggingUrl.length) {
            try {
                if (opts.abcLogging === true) {
                    let error = typeof e === 'string' ? e : e.toString() + '     ' + e.stack.toString();
                    await axios.post(
                        opts.loggingUrl,
                        {exception: error}
                    );
                }
            } catch (err) {
                console.log(err);
            }
        }
    }
};
